// Стили для слайдера
.swiper {
  width: 58%;
  margin: 0 2% !important;
}

.swiper_wrap {
  height: 100%;
  width: 100%;
  background-color: #232323;
}

.swiper-slide {
  text-align: center;
  font: 400 18px "Myriad Pro - Regular";
  display: flex;
  justify-content: center;
  align-items: center;
  color: #ffffff;
  background-color: #232323;
}

.swiper__slide_1 {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  padding: 59px 9% 49px 7.5%;
}

.slide_1__wrap {
  height: inherit;
  width: 58%;
  text-align: left;
  font: 400 16px/20px "Myriad Pro - Light Semi Ext";
  color: #777777;
}

.slide_1__wrapForHeading {
  min-height: 113px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-top: 68px;
  color: #f3f1f1;
}

.slide_1__heading {
  font: 400 40px/42px "Myriad Pro - Regular";
}

.swiper-pagination-bullet {
  opacity: 1 !important;
  border: 2px solid #ffffff;
  width: 8px;
  height: 8px;
}

:root {
  --swiper-theme-color: #ffffff !important;
}

.swiper-pagination-horizontal {
  bottom: 33px !important;
}

.slide_1__text {
  font: 300 18px/20px "Myriad Pro - Light Semi Ext";
}

.slide_1__link {
  width: fit-content;
  text-decoration: none;
  display: block;
  margin: 65px 0;
}

.slide_1__button {
  height: 35px;
  width: 129px;
  text-align: center;
  font: 400 18px/35px "Myriad Pro - Regular";
  color: #ffffff;
  border-radius: 18px;
  background-color: #ffaf2d;
  border: none;
  cursor: pointer;
}

// Убираю стандартные навигационные стрелки
// Правую
.swiper-button-next::after,
.swiper-rtl .swiper-button-prev::after {
  content: none !important;
}
// И левую
.swiper-button-prev::after,
.swiper-rtl .swiper-button-next::after {
  content: none !important;
}

// CSS для моих стрелок
.fa-angle-left,
.fa-angle-right {
  font-size: xx-large;
}

.fa-angle-left:hover,
.fa-angle-right:hover {
  color: #ffaf2d;
}

@media screen and (max-width: 1100px) {
  .slide_1__heading {
    font-size: 30px;
    line-height: 30px;
  }

  .slide_1__text {
    font-size: 16px;
    line-height: 16px;
    margin-top: 15px;
  }

  .slide_1__link {
    font-size: 14px;
    margin: 37px 0;
  }

  .slide_1__button {
    font-size: 14px;
  }
}

@media screen and (max-width: 940px) {
  .swiper__slide_1 {
    padding-right: 2%;
    padding-left: 2%;
  }

  .slide_1__heading {
    font-size: 24px;
    line-height: 24px;
  }

  .slide_1__text {
    font-size: 15px;
    line-height: 15px;
    margin-top: 15px;
  }

  .slide_1__link {
    margin: 70px 0;
  }

  .slide_1__button {
    font-size: 12px;
  }
}

@media screen and (max-width: 768px) {
  .swiper {
    margin: 0 !important;
    width: 100vw;
    align-self: center;
  }

  .swiper-slide {
    height: auto !important;
  }

  .swiper__slide_1 {
    padding: 59px 9% 49px 7.5%;
  }

  .slide_1__heading {
    font-size: 34px;
    line-height: 34px;
  }

  .slide_1__text {
    font-size: 22px;
    line-height: 22px;
    margin-top: 15px;
  }

  .slide_1__button {
    font-size: 18px;
  }

  .slide_1__link {
    margin: 37px 0 65px;
  }

  // переместил стрелки с середины вниз в мобильной версии
  .swiper-button-next,
  .swiper-button-prev {
    top: initial !important;
    bottom: 20px !important;
    z-index: 11 !important;
  }
}
